.modal-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  z-index: 999;
  display: flex;
  justify-content: center;
  align-items: center;
  color: var(--light-shades)
}

.modal-content {
    /* background-color: white; */
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    padding: var(--padding);
    border-radius: var(--border-radius);
    gap: var(--gap);
    background-color: var(--dark-shades);
    border: none;
    box-shadow: 1px 1px 15px rgba(0,0,0,1);
    max-width: 40%;
}

.modal-container img {
    width: 100%;
    height: 300px;
    object-fit: cover;
}

.span2 {
    grid-column: span 2;
}

@media screen and (max-width: 1200px) {
    .modal-content {
        max-width: 80%;
    }
}

@media screen and (max-height: 737px) {
    .modal-content img {
      height: 200px;
    }
  }